body {
  margin: 0;
  font-family: 'Roboto Slab', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
  font-family: 'Roboto Slab', serif;
  width: 100%;
}

.nav-bar {
  background-color: rgba(255, 255, 255, 0.8) !important;
}

.nav-link {
  color: #051d52 !important;
  font-size: 20px !important;
  margin-left: 10px !important;
}

.nav-link:hover {
  color: #517089 !important;
}

.nav-bar__name {
  font-weight: bold !important;
  color: #517089 !important;
  font-size: 24px !important;
}

.nav-bar__name:hover {
  color: #051d52 !important;
}

.nav-bar__container {
  margin-left: 100px !important;
  margin-right: 10px !important;
}

@media only screen and (max-width: 450px) {
  .nav-bar__container {
    margin-left: auto !important;
    margin-right: auto !important;
  }
}

.project {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.project__image {
  background-color: #051d52;
  padding: 20px;
  border-radius: 20px;
}

.project__image__thumbnail {
  height: 200px !important;
}

.project_image__large {
  max-width: 75%;
  border-radius: 20px;
}

.project_image-typography {
  height: 400px !important;
}

.project h1 {
  color: #051d52;
  margin-bottom: 20px;
}

.project p {
  color: #517089;
  font-size: 20px;
  margin-top: 20px;
}

.project__bottom {
  display: flex;
  justify-content: center;
}

.project9 {
  margin-top: 20px;
}

.project__bottom__anchor {
  padding: 15px;
  background-color: #051d52;
  border-radius: 20px;
  max-width: 200px;
}

.project__bottom__anchor a {
  color: #ffffff;
  text-decoration: none;
}

.project__bottom__anchor a:hover {
  color: #517089;
}

.img-thumbnail {
  border: none !important;
}

.project4__image {
  display: flex;
  justify-content: center;
}

.project__image__thumbnail-2 {
  height: 400px !important;
}

.project10__image {
  max-width: 75%;
  max-height: 500px;
  border-radius: 20px;
}

.about-me {
  display: flex;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 20px;
}

.about-me__row {
  display: flex;
  align-items: center;
}

.about-me__row__image {
  max-height: 400px;
  border-radius: 20px;
}

.about-me__info {
  font-size: 20px;
  padding-top: 25px;
  padding-bottom: 25px;
  border-radius: 20px;
  background-color: #517089;
  color: #051d52;
}

.about-me__info__text {
  padding-left: 25px;
  padding-right: 25px;
}

@media only screen and (min-width: 450px) {
  .about-me__row__image {
    max-height: 600px;
  }

  .about-me__info {
    padding: 25px;
  }
}

.contact {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  width: 100%;
}

.contact__row {
  width: 100%;
}

.contact__info {
  width: 100%;
  font-size: 20px;
  color: #051d52;
}

.contact__form {
  width: 100%;
}

.contact__form__label {
  font-size: 18px;
  color: #051d52;
  font-weight: bold;
}

.contact__form__btn {
  color: #051d52 !important;
  background-color: #517089 !important;
  font-weight: bold !important;
  font-family: 'Roboto Slab', serif !important;
  outline: none !important;
  border: none !important;
}

.contact__message {
  margin-top: 10px;
}

.contact__message-error {
  color: red;
  margin-top: 10px;
}

.heading {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 50px;
}

.heading h1 {
  color: #051d52;
}

.heading h3 {
  color: #517089;
}

.heading__name {
  font-size: 4rem;
}

.project-cards {
  margin-top: 20px;
  margin-bottom: 20px;
}

.project-cards .col {
  display: flex;
  justify-content: center;
}

.project-card {
  max-width: 550px;
  border: 1px solid #051d52 !important;
}

.project-card__image {
  height: 500px;
}

.project-card .project-card__overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  padding: 5px;
  color: #ffffff;
  background: #517089;
  text-decoration: none;
  text-align: center;
  transition: opacity 500ms;
  cursor: pointer;
}

.project-card__overlay__title {
  font-size: 24px !important;
  font-weight: bold !important;
}

.project-card__overlay__text {
  font-size: 20px !important;
  font-weight: bold !important;
}

.project-card:hover .project-card__overlay {
  opacity: 0.9;
}

